import {Component, OnInit} from '@angular/core';
import * as Chartist from 'chartist';
import {ApiService} from '../api/api.service';
import {DatePipe} from '@angular/common';
import {NgxSpinnerService} from 'ngx-spinner';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
    month = 0;
    year = 0;
    months = ['Janvier', 'Février', 'Mars', 'Avril', 'Mai', 'Juin', 'Juillet', 'Août', 'Septembre', 'Octobre', 'Novembre', 'Décembre'];
    years = [];
    account: any;
    stats:any;
    doctors:any;
    count_sms : any;
    user_sms:any;
    selectedMonth: string = '';
    gains = {
        cost: 0,
        fees: 0,
        users: 0,
        appointments: 0,
        currency: 'XAF'
    };

    constructor(public api: ApiService, public datepipe: DatePipe, private spinner: NgxSpinnerService) {
    }

    startAnimationForLineChart(chart) {
        let seq: any, delays: any, durations: any;
        seq = 0;
        delays = 80;
        durations = 500;

        chart.on('draw', function (data) {
            if (data.type === 'line' || data.type === 'area') {
                data.element.animate({
                    d: {
                        begin: 600,
                        dur: 700,
                        from: data.path.clone().scale(1, 0).translate(0, data.chartRect.height()).stringify(),
                        to: data.path.clone().stringify(),
                        easing: Chartist.Svg.Easing.easeOutQuint
                    }
                });
            } else if (data.type === 'point') {
                seq++;
                data.element.animate({
                    opacity: {
                        begin: seq * delays,
                        dur: durations,
                        from: 0,
                        to: 1,
                        easing: 'ease'
                    }
                });
            }
        });

        seq = 0;
    };

    startAnimationForBarChart(chart) {
        let seq2: any, delays2: any, durations2: any;

        seq2 = 0;
        delays2 = 80;
        durations2 = 500;
        chart.on('draw', function (data) {
            if (data.type === 'bar') {
                seq2++;
                data.element.animate({
                    opacity: {
                        begin: seq2 * delays2,
                        dur: durations2,
                        from: 0,
                        to: 1,
                        easing: 'ease'
                    }
                });
            }
        });

        seq2 = 0;
    };

    ngOnInit() {
        var date = new Date(), y = date.getFullYear(), m = date.getMonth();
        this.month = m;
        this.year = y;
        this.loadSum(false);
        this.getDoctor();
        this.searchSMS();
        this.api.getData().subscribe(response => {
            this.account= response.account.data;
            this.stats= response.statistics.data;
        });
        /* ----------==========     Daily Sales Chart initialization For Documentation    ==========---------- */

        const dataDailySalesChart: any = {
            labels: ['M', 'T', 'W', 'T', 'F', 'S', 'S'],
            series: [
                [12, 17, 7, 17, 23, 18, 38]
            ]
        };

        const optionsDailySalesChart: any = {
            lineSmooth: Chartist.Interpolation.cardinal({
                tension: 0
            }),
            low: 0,
            high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {top: 0, right: 0, bottom: 0, left: 0},
        }

        var dailySalesChart = new Chartist.Line('#dailySalesChart', dataDailySalesChart, optionsDailySalesChart);

        this.startAnimationForLineChart(dailySalesChart);


        /* ----------==========     Completed Tasks Chart initialization    ==========---------- */

        const dataCompletedTasksChart: any = {
            labels: ['12p', '3p', '6p', '9p', '12p', '3a', '6a', '9a'],
            series: [
                [230, 750, 450, 300, 280, 240, 200, 190]
            ]
        };

        const optionsCompletedTasksChart: any = {
            lineSmooth: Chartist.Interpolation.cardinal({
                tension: 0
            }),
            low: 0,
            high: 1000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {top: 0, right: 0, bottom: 0, left: 0}
        }

        var completedTasksChart = new Chartist.Line('#completedTasksChart', dataCompletedTasksChart, optionsCompletedTasksChart);

        // start animation for the Completed Tasks Chart - Line Chart
        this.startAnimationForLineChart(completedTasksChart);


        /* ----------==========     Emails Subscription Chart initialization    ==========---------- */

        var datawebsiteViewsChart = {
            labels: ['J', 'F', 'M', 'A', 'M', 'J', 'J', 'A', 'S', 'O', 'N', 'D'],
            series: [
                [542, 443, 320, 780, 553, 453, 326, 434, 568, 610, 756, 895]

            ]
        };
        var optionswebsiteViewsChart = {
            axisX: {
                showGrid: false
            },
            low: 0,
            high: 1000,
            chartPadding: {top: 0, right: 5, bottom: 0, left: 0}
        };
        var responsiveOptions: any[] = [
            ['screen and (max-width: 640px)', {
                seriesBarDistance: 5,
                axisX: {
                    labelInterpolationFnc: function (value) {
                        return value[0];
                    }
                }
            }]
        ];
        var websiteViewsChart = new Chartist.Bar('#websiteViewsChart', datawebsiteViewsChart, optionswebsiteViewsChart, responsiveOptions);

        //start animation for the Emails Subscription Chart
        this.startAnimationForBarChart(websiteViewsChart);
    }


    getDoctor(){
        this.api.performGetRequest('users?orderBy=first_name&search=user_type:doctor&limit=1000').then(res=>{
            this.doctors = res['data'];
        })
    }


    loadSum(calc = false) {
        //this.loader_consultation = true;
        //var date = new Date(), y = date.getFullYear(), m = date.getMonth();
        var firstDay = new Date(this.year, this.month, 1);
        var lastDay = new Date(this.year, this.month + 1, 0);
        //if (calc) {
            this.spinner.show();
        //}
        this.api.performGETCall('searchappointments', {
            limit: 0,
            field: null,
            value: null,
            start: this.datepipe.transform(firstDay, 'yyyy-MM-dd'),
            end: this.datepipe.transform(lastDay, 'yyyy-MM-dd')
        }, true).subscribe(res => {
            this.gains.cost = res.data.cost;
            this.gains.users = res.data.type;
            this.gains.appointments = res.data.address;
            //this.gains.fees = res.data.status;
            this.gains.currency = res.data.currency;
            this.spinner.hide();
            //this.loader_consultation = false;
            //this.consultations = res.data;
        }, err => {
            this.spinner.hide();
            //this.loader_consultation = false;
            this.api.handleErrors(err.error.message);
        });
    }

    onDoctorSelectionChange(event: any): void {
        this.api.performGetRequest(`count-sms?doctorId=${event.target.value}`).then(res=>{
            this.count_sms = res;
        })
    }

    searchSMS(){
        const year = this.selectedMonth.split('-')[0];
        const month = this.selectedMonth.split('-')[1];
        let params='count-sms';
        if(year && month){
            params=`count-sms?month=${month}&year=${year}`;
        }
        this.api.performGetRequest(params).then(res=>{
            this.user_sms =res;
        })
    }

    calculateTotalSMS(): number {
        return this.user_sms.reduce((total, sms) => total + sms.appointment_count * 3, 0);
    }

}
